.component-five-day-forecast {}

.forecast-day {
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 0.25rem;
}

.forecast-day:nth-child(1) {
	opacity: calc((1 / 6) * 6);
}

.forecast-day:nth-child(2) {
	opacity: calc((1 / 6) * 5);
}

.forecast-day:nth-child(3) {
	opacity: calc((1 / 6) * 4);
}

.forecast-day:nth-child(4) {
	opacity: calc((1 / 6) * 3);
}

.forecast-day:nth-child(5) {
	opacity: calc((1 / 6) * 2);
}

.forecast-day:nth-child(6) {
	opacity: calc((1 / 6) * 1);
}


.forecast-day__day,
.forecast-day__icon,
.forecast-day__temp {
	-ms-flex-preferred-size: 25%;
	    flex-basis: 25%;
	text-align: right;
}



