.component-clock-widget {
	font-weight: 300;
}

.component-clock-widget .clock__separator {
	-webkit-animation: blink 2s infinite;
	        animation: blink 2s infinite;
	-webkit-transition: all 1s;
	-o-transition: all 1s;
	transition: all 1s;
}

@-webkit-keyframes blink {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.25;
	}
}

@keyframes blink {
	0%,
	100% {
		opacity: 1;
	}

	50% {
		opacity: 0.25;
	}
}



